import { SVGPathData } from "svg-pathdata"


export const V = (y) => ({
  type: SVGPathData.VERT_LINE_TO,
  y,
  relative: false,
})

export const S = (x2, y2, x, y) => ({
  type: SVGPathData.SMOOTH_CURVE_TO,
  x2,
  y2,
  x,
  y,
  relative: false,
})

export const M = (x, y) => ({
  type: SVGPathData.MOVE_TO,
  x,
  y,
  relative: false,
})
export const m = (x, y) => ({
  type: SVGPathData.MOVE_TO,
  x,
  y,
  relative: true,
})
export const L = (x, y) => ({
  type: SVGPathData.LINE_TO,
  x,
  y,
  relative: false,
})
export const l = (x, y) => ({
  type: SVGPathData.LINE_TO,
  x,
  y,
  relative: true,
})

export const Z = () => ({ type: SVGPathData.CLOSE_PATH })
